import React, { Component  } from 'react';
import './App.css';
import Navigation from './Components/Navigation/Navigation';
import SignIn from './Components/SignIn/SignIn';
import Register from './Components/Register/Register';
import FaceRecognition from './Components/FaceRecognition/FaceRecognition';
import Logo from './Components/Logo/Logo';
import 'tachyons';
import ImageLinkForm from './Components/ImageFormLink/ImageLinkForm';
import Rank from './Components/Rank/Rank';
import ParticlesBg from 'particles-bg'


//   apiKey:'c5daa05f871e4d578961a7d4c3f25e7e'
// });




const initialState ={
      input:'',
      imageUrl:'',
      boxes:[],
      route: 'signin',
      isSignedIn:false,
      user:{
          id:'',
          Name:'',
          Email:'',
          entries:0,
          joined:'',
      }
}

class App extends Component {
  constructor(){
    super();
    this.state = initialState;

    }
  
  
  loadUser=(data)=>{
    console.log(data.name)
    console.log(data.email)
    console.log("new user data here",data)
    this.setState({user : {
          id:data.id,
          name:data.name,
          email:data.email,
          entries:data.entries,
          joined:data.joined
    }})
  }
  calculateFaceLocation =(data)=>{
    // const clarifaiFace = data.outputs[0].data.regions[0].region_info.bounding_box;
    const clarifaiFaces = data.outputs[0].data.regions.map(face => {
      return face.region_info.bounding_box;
    });
    const image =document.getElementById('inputimage');
    const width= Number(image.width);
    const height= Number(image.height);
    const faceCoordinates = clarifaiFaces.map(face => {
    return {
      leftCol: face.left_col * width,
        topRow: face.top_row * height,
        rightCol: width - face.right_col * width,
        bottomRow: height - face.bottom_row * height,

    }
  });
    return faceCoordinates;
  }
  displayFaceBox =(faceboxes) =>{
    console.log(faceboxes);
    this.setState({boxes:faceboxes});
  }

  OnInputChange = (event)=>{
    this.setState({input:event.target.value});
  }

  onButtonSubmit =() =>{
    this.setState({imageUrl:this.state.input})
    fetch('https://erin-friendly-sawfish.cyclic.app/imageurl',{
      method:'post',
      mode:'cors',
      headers:{'Access-Control-Allow-Origin': '*',
        'Content-Type':'application/json'},
      body:JSON.stringify({
        input:this.state.input
      })
    })
    .then(response=>response.json())

    .then(response => {
        if(response){
          fetch('https://erin-friendly-sawfish.cyclic.app/image',{
            method:'put',
            mode:'cors',
            headers:{
              'Access-Control-Allow-Origin': '*',
              'Content-Type':'application/json'},
            // headers
            body:JSON.stringify({
              id:this.state.user.id
            })
          })
          .then(response=>response.json())
          .then(count=>{
            this.setState(Object.assign(this.state.user,{entries:count}))
          })
          .catch(console.log)
        }
        this.displayFaceBox(this.calculateFaceLocation(response))
      })
      .catch(err=>console.log(err));}

  onRouteChange=(route)=>{
    if (route==='signout'){
      this.setState(initialState);
    }
    else if (route==='home') {
      this.setState({isSignedIn:true}) ;}
    this.setState({route:route});
  }


  render(){
    return (
      <div className="App">
        <ParticlesBg type="square" bg={true} />
        <Navigation isSignedIn={this.state.isSignedIn} onRouteChange={this.onRouteChange}/>
        { this.state.route ==='home' ?
          <div>
          <Logo />
          <Rank name={this.state.user.name} entries={this.state.user.entries}/>
          <ImageLinkForm 
            OnInputChange={this.OnInputChange} onButtonSubmit={this.onButtonSubmit} />
        <FaceRecognition boxes={this.state.boxes} imageUrl={this.state.imageUrl} /></div>
          :(
            this.state.route ==='signin'?
            <SignIn loadUser={this.loadUser} onRouteChange={this.onRouteChange} />
            :
            <Register loadUser={this.loadUser} onRouteChange={this.onRouteChange} />
          )
          
          
        
}
      </div>
    );
}}

export default App;
