import React from "react";
import Tilt from 'react-parallax-tilt';
import './Logo.css'
import brain from './brains.png';


const Logo=()=>{
    return(
        <div className="ma4 mt0 w-10 ">
            <Tilt className="Tilt br2 shadow-2 w50px ">
                <div style={{ height:'50px'}}>
                    <img style={{paddingTop:'5px',height:'50px',width:'50px'}}src={brain}  alt='no logo found'/>
                </div>
            </Tilt>
        </div>    
    );
}

export default Logo;